
.dialog .main-wrapper{z-index:999;background:#FFF;position:absolute;min-width:200px;min-height:100px
}
.dialog.poly{width:100vw;height:100vh;background:none;border:none;position:fixed;top:0;left:0
}
.dialog.poly .mask-layer{position:fixed;left:0;top:0;width:100%;height:100%;background:rgba(0,0,0,0.6);z-index:777
}
.dialog.poly .main-wrapper{position:absolute;left:50%;width:400px;background:#FFF
}


