
.month-select{height:32px;max-width:280px;font-size:18px;line-height:32px;color:#42b983
}
.month-select .float.ileft{float:left;font-size:16px
}
.month-select .float.iright{float:right;font-size:16px
}
.month-select input{font-size:18px;line-height:32px;color:#42b983;width:60px;text-align:center;border:none;background-color:transparent
}
.month-select>i{margin:0 12px;line-height:32px;cursor:pointer;color:#707070
}
.month-select>i:hover{color:#42b983
}
.month-select .disabled{color:#EEE
}


