
.pager{text-align:center;vertical-align:middle;margin:10px 0;position:relative
}
.pager a{cursor:pointer
}
.pager .setter{display:inline-block;vertical-align:middle;line-height:1.5em;float:left
}
.pager .select-wrapper{line-height:30px
}
.pager .select-wrapper .select{z-index:5;line-height:30px;height:32px
}
.pager .go{float:right;display:inline-block;margin-right:20px;position:absolute;top:0;right:0
}
.pager .go input{line-height:30px;border-radius:2px;width:40px;border:1px solid #DDD;text-align:center
}
.pagination-wrapper{width:400px;margin:0 auto
}
.pagination{margin:0;display:-webkit-inline-box;display:-webkit-inline-flex;display:-ms-inline-flexbox;display:inline-flex;vertical-align:middle
}
.pagination i{color:#333
}
.pagination .item:first-child{border-radius:.28571429rem 0 0 .28571429rem
}
.pagination .item:last-child{border-radius:0 .28571429rem .28571429rem 0
}
.pagination .item:last-child:before{display:none
}
.pagination .item{min-width:3em;text-align:center;line-height:30px;border:1px solid #CCC;margin-left:-1px;padding:0 10px;box-sizing:border-box
}
.pagination .active.item{background-color:rgba(0,0,0,0.05);color:rgba(0,0,0,0.95);box-shadow:none
}
.pagination .item.disabled,.pagination .item.disabled:hover{cursor:default;background-color:transparent !important;color:rgba(40,40,40,0.3)
}


