
.date-picker{font-size:12px;position:relative;color:#333;width:205px !important
}
.date-picker *{box-sizing:border-box
}
.date-picker .button-panel{position:absolute;bottom:10px
}
.date-picker .info-sign{position:absolute;right:45px;top:0
}
.date-picker .hidden{display:none
}
.date-picker .calable{position:absolute;display:block;left:0;top:0;width:100%;height:100%;z-index:3
}
.date-picker .range-type a{display:inline-block;line-height:15px;padding:0 5px
}
.date-picker .range-type .active{color:#42b983
}
.date-picker .layer{position:absolute;top:31px;left:0;background:#FFF;width:530px;height:340px;padding:10px;border:1px solid #EEE;z-index:100
}
.date-picker .date-wrapper{position:relative;display:inline-block;width:250px
}
.date-picker .calendar{left:0;top:40px;height:217px
}
.date-picker .range-input{width:100%;text-align:left
}
.date-picker .sm-calender{position:absolute;right:16px;top:10px;width:5px;height:10px;font-size:15px;color:#333
}
.date-picker .date-input{width:238px;line-height:22px
}
.date-picker .close-btn{position:absolute;top:10px;right:10px;font-size:12px;font-weight:bolder;cursor:pointer
}
.date-picker button{min-width:3em
}
.date-picker button.cancel{color:#777 !important;border:0;background:#FFF
}


