
.wild-dialog .dialog{left:30%;top:10%
}
.dialog{position:absolute;min-width:180px;padding:0;border:1px solid rgba(34,36,38,0.2);border-radius:4px;box-sizing:border-box;box-shadow:0 2px 3px rgba(34,36,38,0.15);background-color:#FFF;z-index:80
}
.dialog .header{margin:0;font-size:13px;border-radius:4px 4px 0 0;color:#333;box-sizing:border-box;height:40px;padding:10px 38px 10px 16px;border-bottom:1px solid #e4e4e4;background-color:#f5f5f5;line-height:20px
}
.dialog .content{padding:28px;display:block;min-width:120px
}
.dialog .dialog-wrapper.pad-bottom{position:relative
}
.dialog .footer{padding:12px 16px;bottom:0;border-top:1px solid #e4e4e4;width:100%;box-sizing:border-box
}
.dialog .footer .default-bottom{text-align:right;min-width:100px
}
.dialog .footer button{margin-left:5px;cursor:pointer;width:56px;min-width:20px;line-height:24px;height:24px
}
.dialog .footer .ensure-button:hover{border:1px solid #2bbc89
}
.dialog .footer .cancel-button{padding:0 5px;border:1px solid #FFF;width:40px;background-color:#fff;outline-color:#fff;background-image:none;margin-left:13px;margin-right:0
}
.dialog::backdrop{background-color:rgba(0,0,0,0.2)
}
.dialog .close{position:absolute;cursor:pointer;right:11px;top:12px;font-size:14px;color:#333;z-index:3
}
.dialog.fixed{position:fixed;z-index:999;left:0;top:0
}
.dialog.full{position:fixed;z-index:999;left:0;top:0;width:100%;height:100vh;overflow:auto
}
.dialog.inline{padding-bottom:0;min-width:150px
}
.dialog.inline .close{right:2px;top:2px
}
.dialog.inline .footer{display:none
}
.dialog.inline .content{min-height:20px;padding:5px
}


