
.press-tab{height:30px;display:inline-block;border:1px solid #CCC;border-radius:5px;background-color:#FFF;margin:10px 18px
}
.press-tab a{display:inline-block;width:84px;line-height:28px;border-right:1px solid #CCC;padding:0 5px;text-align:center;background-color:#FFF;float:left;color:#333;background:linear-gradient(#FFF, #ebebeb);cursor:pointer
}
.press-tab a.active{background:linear-gradient(#ebebeb, #FFF);cursor:auto
}
.press-tab a:first-child{border-radius:5px 0 0 5px;border-right:1px solid #CCC
}
.press-tab a:last-child{border-radius:0 5px 5px 0;border-right:0
}


