
.calendar{width:235px;padding:10px;background:#fff;position:absolute;border:1px solid #DEDEDE;border-radius:2px;transition:all .5s ease;color:#333
}
.calendar .calendar-enter,.calendar .calendar-leave{opacity:0;transform:translate3d(0, -10px, 0)
}
.calendar .toast.warn{color:#C00;background:#EEE;font-size:12px;padding:2px 10px;line-height:18px;border:1px solid #CCC;border-radius:2px;position:absolute;display:inline-block;bottom:0;left:30%
}
.calendar:before{position:absolute;left:30px;top:-10px;content:"";border:5px solid rgba(0,0,0,0);border-bottom-color:#DEDEDE
}
.calendar:after{position:absolute;left:30px;top:-9px;content:"";border:5px solid rgba(0,0,0,0);border-bottom-color:#fff
}
.calendar .month-input{width:40px !important;outline:none
}
.calendar .year-input{width:65px !important;outline:none
}
.calendar table{clear:both;width:100%;margin-bottom:10px;border-collapse:collapse;color:#444444
}
.calendar td{margin:2px !important;padding:5px 0;width:14.28571429%;text-align:center;vertical-align:middle;font-size:12px;line-height:125%;cursor:pointer
}
.calendar td:hover{background:#f3f8fa
}
.calendar td.week{pointer-events:none !important;cursor:default !important
}
.calendar td.disabled{color:#c0c0c0;background-color:none;pointer-events:none !important;cursor:default !important
}
.calendar td.valid{color:#42b983;font-size:12px
}
.calendar td.selected{color:#FFF;background-color:#42b983;font-size:12px
}
.calendar thead td{text-transform:uppercase
}
.calendar .timer{margin:10px 0;text-align:center
}
.calendar .timer input{border-radius:2px;padding:5px;font-size:14px;line-height:18px;color:#42b983;width:50px;text-align:center;border:1px solid #efefef
}
.calendar .timer input:focus{border:1px solid #42b983
}
.calendar .calendar-button{text-align:center
}
.calendar .calendar-button button{border:none;cursor:pointer;display:inline-block;min-height:1em;vertical-align:baseline;background:#42b983;color:#fff;margin:0 .25em 0 0;padding:5px 10px;font-size:1em;line-height:1em;text-align:center;border-radius:.3em
}
.calendar .calendar-button button.cancel{background:#efefef;color:#666
}


