
.loading-box.mask{width:100%;height:100%;background:rgba(0,0,0,0.2)
}
.loading-box.full{position:absolute;width:100%;height:100%;position:fixed
}
.loading-box.full .content{position:absolute;top:50%;left:49vw
}
.icon-snake{border:4px solid transparent;border-top-color:#26a2ff;border-left-color:#26a2ff;border-bottom-color:#26a2ff;height:28px;width:28px;display:inline-block;border-radius:50%
}
.loading-box{z-index:10001;top:0;right:0;bottom:0;left:0;position:relative;min-height:5em;vertical-align:top;background:rgba(0,0,0,0.01)
}
.loading-box .close{font-size:1.5em
}
.loading-box i.icon{font-size:3em;color:#26a2ff;margin-left:-20px;margin-top:-20px
}
.loading-box div.icon{width:100%;left:0;text-align:center
}
.loading-box .icon{position:absolute;left:50%;top:50%;animation:loading 1s infinite step-start
}
.loading-box .content{text-align:center;margin-top:6em
}
.loading-box .close{position:absolute;top:5px;right:5px
}
@keyframes loading{
0%{transform:rotate(0deg)
}
12.5%{transform:rotate(45deg)
}
25%{transform:rotate(90deg)
}
37.5%{transform:rotate(135deg)
}
50%{transform:rotate(180deg)
}
62.5%{transform:rotate(225deg)
}
75%{transform:rotate(270deg)
}
87.5%{transform:rotate(315deg)
}
}


