
.select-wrapper{position:relative;display:inline-block;box-sizing:border-box
}
.select-wrapper .select-tip{position:absolute;top:0;left:0;line-height:30px;padding:0 10px;color:#333
}
.select-wrapper .drop-icon{position:absolute;right:8px;top:10px;font-size:12px;color:#777
}
.select-wrapper .select{padding:0 10px;padding-right:40px;background-color:transparent;box-sizing:border-box;border:1px solid #c0c8c9;border-radius:4px;position:relative;background-repeat:no-repeat;background-size:1.5rem 1rem;background-position:right center;font:inherit;color:inherit;z-index:3;height:30px;-webkit-appearance:none;-moz-appearance:none;appearance:none
}
.select-wrapper .select:focus{outline:none
}


