
.info-sign{position:relative;line-height:1.5em;padding:5px 0;display:inline
}
.info-sign .info{width:100px;min-height:50px;box-sizing:border-box;position:absolute;background:#FFF;display:block;padding:8px 16px;border:1px solid rgba(34,36,38,0.2);border-radius:4px;box-shadow:0 2px 3px rgba(34,36,38,0.15);color:#333;font-size:13px;font-weight:normal;line-height:1.8;z-index:999
}
.info-sign .info:after,.info-sign .info:before{content:'';position:absolute;border:6px solid transparent
}
.info-sign .info.left{left:-110px;top:-25px
}
.info-sign .info.left:after,.info-sign .info.left:before{right:-15px;top:30px;margin-bottom:-20px;border-left-width:10px
}
.info-sign .info.left:before{margin-right:-2px;border-left-color:rgba(34,36,38,0.2)
}
.info-sign .info.left:after{border-left-color:#FFF
}
.info-sign .info.right{left:20px;top:-25px
}
.info-sign .info.right:after,.info-sign .info.right:before{left:-15px;top:30px;margin-bottom:-20px;border-left-width:10px
}
.info-sign .info.right:before{margin-left:-2px;border-right-color:rgba(34,36,38,0.2)
}
.info-sign .info.right:after{border-right-color:#FFF
}
.info-sign .info.bottom{top:32px;left:-103px;width:200px
}
.info-sign .info.bottom:after,.info-sign .info.bottom:before{content:'';position:absolute;left:102px;top:-16px;border:6px solid transparent;border-bottom-width:10px
}
.info-sign .info.bottom:before{margin-top:-2px;border-bottom-color:rgba(34,36,38,0.2)
}
.info-sign .info.bottom:after{border-bottom-color:#FFF
}
.info-sign .info.top{top:-55px;left:-103px;width:200px
}
.info-sign .info.top:after,.info-sign .info.top:before{content:'';position:absolute;left:102px;bottom:-16px;border:6px solid transparent;border-top-width:10px
}
.info-sign .info.top:before{margin-bottom:-2px;border-top-color:rgba(34,36,38,0.2)
}
.info-sign .info.top:after{border-top-color:#FFF
}


