
.command-list{position:relative;display:inline-block;min-width:100px;border:1px solid #DDD;border-radius:2px;cursor:pointer
}
.command-list .layer{min-width:86px;position:absolute;top:28px;left:0;background:#FFF;z-index:777;padding:5px 0;border:1px solid #DDD
}
.command-list li{padding:0 10px;line-height:2em
}
.command-list li:hover{background-color:#EEE;color:#39bd8b
}
.command-list:disabled{color:#EEE
}
.command-list:disabled .layer{display:none !important
}


