
.line-box{position:relative;border:1px solid #e4e4e4;border-radius:3px;min-height:10rem;box-sizing:content-box;overflow:hidden
}
.line-box .line-list{display:block;width:30px;text-align:center;background-color:#e3e3e3;padding-top:2px;font-size:13px;height:100%;overflow:hidden;position:absolute;top:0;left:0
}
.line-box .line-list li{height:25px;line-height:25px;text-align:center
}
.line-box .textarea-container{padding-left:35px;box-sizing:border-box
}
.line-box textarea{width:100%;font-size:13px;border:0;outline:none;line-height:25px;min-height:200px;overflow:hidden;padding-left:5px
}


