
.simple-table{border:1px solid #e4e4e4;border-top:none;border-right:none;position:relative
}
.simple-table .plugin-items{display:inline-block;position:absolute;right:5px;top:10px
}
.simple-table .table-body.scroll{max-height:80vh;overflow:scroll
}
.simple-table thead.head-row{width:100%;position:static;background:#EEE
}
.simple-table table{border-spacing:0;width:100%
}
.simple-table thead{background-color:#f5f5f5
}
.simple-table thead tr td{line-height:2em;vertical-align:baseline;background:#EEE
}
.simple-table td[data-col="select"]{width:50px
}
.simple-table td{color:#333;font-size:12px;line-height:1.5em;border-top:1px solid #e4e4e4;border-right:1px solid #e4e4e4;padding:8px 5px;position:relative;vertical-align:middle
}


