
.stepper{line-height:25px
}
.stepper li{list-style:none;display:inline-block
}
.stepper .badget{display:inline-block;width:25px;height:25px;line-height:25px;text-align:center;border-radius:12.5px;background-color:#CCC;color:#FFF
}
.stepper .badget:hover,.stepper .badget.active{background-color:#26c899
}
.stepper .text{display:inline-block
}
.stepper .line{width:100px;border:0;height:1px;vertical-align:middle;margin:0 5px;background-color:#CCC;display:inline-block
}
.stepper li:last-child .line{display:none
}


