
.post-form{position:relative
}
.post-form input,.post-form textarea{outline:none;border:1px solid #bbb
}
.post-form .on input:invalid{border:1px solid red
}
.post-form .on textarea:valid{border:1px solid green
}
.post-form .on textarea:invalid{border:1px solid red
}
.post-form .on input:disabled{border:1px solid #CCC
}


