
.range-bar{position:relative
}
.range-bar .flag{width:2px;height:100%;margin-left:-1px;border-radius:8px;background-color:orange;position:absolute;top:0
}
.range-bar .flag span{display:inline-block;width:100px;position:absolute;top:1em;left:-1em
}
.range-bar input[type=range]{width:100%
}
.range-bar .bar{background-color:deepskyblue;height:10px;border-radius:5px
}
.range-bar .range-box{border:1px solid #EEE;border-radius:5px;background-color:#EEE;width:100%;display:block;position:relative
}


