
.tree-wrapper ul{margin-left:1rem;list-style-type:none;padding:10px
}
.tree-wrapper .list-icon{display:inline;margin-right:10px;color:#777
}
.tree-wrapper li{position:relative;cursor:pointer;box-sizing:border-box
}
.tree-wrapper li span{text-decoration:none;color:#777;font-size:14px;display:inline-block;margin-left:15px;width:100%
}
.tree-wrapper li span:hover{color:#42b983
}
.tree-wrapper li i{color:#777;position:absolute;top:4px;font-size:.6em
}
.tree-wrapper .expand-transition{transition:all .7s ease;height:auto;padding:10px
}
.tree-wrapper .expand-enter,.tree-wrapper .expand-leave{height:0;padding:0 10px;opacity:0
}


