
.select-timer-control{font-size:13px;width:720px;padding:10px;margin:0 auto
}
.select-timer-control .tip-bar{height:18px;line-height:18px;padding:10px 0
}
.select-timer-control .tip-bar .action{display:inline-block
}
.select-timer-control .tip-bar .action span{display:inline-block;padding:0 5px;cursor:pointer
}
.select-timer-control .tip-bar .tip{display:inline-block;float:right;margin-left:20px
}
.select-timer-control .tip-bar .tip span{display:inline-block;height:13px;margin-top:2px;width:13px;vertical-align:top;margin-right:5px
}
.select-timer-control .tip-bar .tip .selected-tip{background-color:#f7873d
}
.select-timer-control .tip-bar .tip .unselected-tip{background-color:#ccc
}
.select-timer-control .week-container .day-line{display:block
}
.select-timer-control .week-container .day-th-line{margin-top:16px
}
.select-timer-control .week-container .day-container li{display:inline-block;width:25px;height:16px;font-size:0
}
.select-timer-control .week-container .day-container .week-th{width:88px;font-size:13px;background-color:#fff;box-sizing:border-box;height:25px;line-height:25px
}
.select-timer-control .week-container .day-container .week-th label{cursor:pointer
}
.select-timer-control .week-container .day-container .hour-th,.select-timer-control .week-container .day-container .hour-td{width:25px;height:25px;vertical-align:bottom;overflow:hidden;border:1px solid #fff;border-width:1px 1px 0 0;box-sizing:border-box
}
.select-timer-control .week-container .day-container .hour-td{color:#fff;text-align:center;line-height:25px;font-size:12px;cursor:pointer
}
.select-timer-control .week-container .day-container .selected-0{background-color:#ccc
}
.select-timer-control .week-container .day-container .selected-1{background-color:#f7873d
}
.select-timer-control .week-container .day-container .selected-1:hover{background-color:#fbd4ba
}
.select-timer-control .week-container .day-container .hour-th{font-size:14px;text-align:center;line-height:30px;cursor:pointer;color:#ccc
}
.select-timer-control .week-container .day-container .hour-th:hover,.select-timer-control .week-container .day-container .hour-th.selected{color:#f7873d
}


