
td.row-select .cell{z-index:99
}
td.row-select .select-flip{position:absolute;z-index:9;width:80px;height:75px;padding:6px;line-height:2rem;border:1px solid #DDD;top:-1px;left:-1px;border-radius:2px;box-shadow:2px 2px #CCC;background-color:#EEE
}
td.row-select .select-flip label{width:auto !important;display:block
}


