
.popup{position:fixed;z-index:9999;bottom:10px;right:10px;font-size:12px;border:1px solid #CCC;border-radius:5px;min-width:250px;background:#FFF
}
.popup .close{position:absolute;top:5px;right:5px
}
.popup .title{border-bottom:1px solid #CCC;line-height:2em;padding:5px 10px
}
.popup .content{padding:40px 10px 20px;min-height:30px;max-width:300px
}
.popup .footer{height:40px
}
.popup .close-button{float:right;color:#FFF;border:1px solid #39bd8b;background:linear-gradient(rgba(57,189,139,0.7), #39bd8b)
}


