
.tab-list{background:#f5f5f5;border:1px solid #e4e4e4;border-right:0;border-left:0;color:#333;padding-left:16px;font-size:14px;position:relative
}
.tab-list ul{list-style:none;padding:0;margin:0
}
.tab-list .tab{display:inline-block;vertical-align:top
}
.tab-list .tab.double.active{height:98px
}
.tab-list .tab.active>a{position:relative;box-sizing:border-box;border-bottom:3px solid #39bd8b;color:#39bd8b;font-weight:bold;z-index:1
}
.tab-list .tab a{display:inline-block;height:51px;margin-bottom:-1px;line-height:50px;padding:0 16px
}
.tab-list .tab .sub-tab{position:absolute;left:0
}
.tab-list .sub-tab{box-sizing:border-box;height:24px;width:100%;padding:13px 16px;border-top:1px solid #e4e4e4;color:#333;font-size:13px;font-weight:normal
}
.tab-list .sub-tab ul{padding:0 16px
}
.tab-list .sub-tab li{display:inline-block;margin-right:10px;padding:0 10px;border-radius:3px
}
.tab-list .sub-tab li.focus{border:1px solid #39bd8b;color:#39bd8b;padding:0 9px
}
.tab-list .sub-tab a{height:22px;padding:0;line-height:22px
}


